html {
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
}

canvas {
  max-width: 473px;
  max-height: 473px;
}

#app {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-top {
  margin: 65px;
}

.logo-bottom {
  margin: 32px;
}

.delimiter {
  width: 13rem;
  border: 3px solid #f0d4cb;
  margin: 3.5rem;
}

.title {
  max-width: 38rem;
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
  margin: 0;
}

.desc {
  max-width: 45.875rem;
  text-align: center;
}
